@import 'vars';
@import "mixins";

.page-title {
    margin: 35px 0 35px 0;
    &--first {
        margin: 0;
        margin-top: 25px;
    }
}

.classes-tabs {
    &__controls {
        text-align: right;
        margin-bottom: 5px;
        &-item {
            box-sizing: content-box;
            vertical-align: top;
            margin-left: 10px;
            height: 37px;
            border-radius: 2px;
            background-color: #ffffff;
            display: inline-block;
            position: relative;
            &:before {
                content: '';
                width: 0;
                height: 3px;
                background: #ececec;
                position: absolute;
                margin: auto;
                bottom: -2px;
                left: 0;
                right: 0;
                transition: .5s;
            }
            .classes-tabs__controls-link {
                color: map_get($gray-light, default);
                padding: 10px 20px;
                font-size: 14px;
                display: inline-block;               
                .calendar__icon {
                    background-image: url(data:image/svg+xml;base64,);
                    // fill: rgba(0, 172, 193, 0.5);
                    fill: map_get($gray-light, default); 
                    vertical-align: middle;
                    display: inline-block;
                    background-size: 0;
                    background-repeat: no-repeat;
                    svg {
                        display: block;
                        max-width: 100%;
                        max-height: 100%;
                        transition: .3s;
                    }
                }
                .classes-tabs__controls-title {
                    vertical-align: middle;
                    margin-left: 10px;
                }
                .view-switch__icon--list {
                    // fill: rgba(0, 172, 193, 0.5);
                    svg {
                        rect {
                            fill: map_get($gray-light, default); 
                        }
                    }
                    vertical-align: middle;
                    transition: .3s;
                }
            }
        }
        .classes-tabs__controls-item.active {
            &:before {
                width: 100%;
                // background: rgba(0, 172, 193, 0.5);
                background-color: map_get($gray, default); 
            }
            .classes-tabs__controls-link {
                color:  map_get($gray, default);
                span {
                    svg *{
                        fill:  map_get($gray, default);
                    }
                }
            }
        }
        // .classes-tabs__controls-item:hover {
        //     .classes-tabs__controls-link {
        //         color:  map_get($gray, default);
        //         span {
        //             fill:  map_get($gray, default);
        //         }
        //     }
        // }
    }
    .classes-tabs__list {
        .classes-tabs__item {
            display: none;
            &.active {
                display: block;
            }
        }

    }    
}

.classes__form {
    background-color: #fff;
    width: 680px;
    padding: 40px 85px 45px 60px;
    .main-form__row {
        margin: 0 0 30px 0;
    }
    .main-form__row--float {
        .form-field-wrap {
            width: auto;
        }
    }
}


.timetable__content {
    background-color: #fff;
    padding: 20px 20px 90px 95px;
    // margin-bottom: 35px;
    .table {
        width: 930px;
        border: 1px solid #ececec;
        thead {
            background-color: #f3f3f3;
            td {
                padding: 20px 26px 20px;
                border: none;
                font-size: 16px;
                color: #2b2b2b;
            }
            .table__cel-left {
                font-size: 18px;
                font-weight: 700;
            }
            .table__cel-right {
                text-align: right;
            }
        }

        tbody {
            font-size: 14px;

            td {
                border: 1px solid #ececec;
                font-weight: 400;
                color: map_get($gray, default);
                vertical-align: middle;
            }
            .blue-btn, .gray-btn {
                font-size: 13px;
                width: 150px;
                padding: 0 10px;
            }
            .table__cel-color {
                background-color:  rgba(255, 255, 169, 0.55);
                td {
                    padding: 20px 15px 20px 20px;
                }
                .table__row-price, .table__row-button {
                    padding-right: 0;

                }
            }
            .table__cel.table__cel-color {
                .table__row-nameClasses {
                    color: map_get($gray, default);
                }
            }
            .table__row-time {
                width: 100px;
            }
            .table__row-nameClasses {
                width: 305px;
                font-family:  $merri-font;
                font-size: 14px;
                font-weight: 500;
            }
            .table__row-quantityPlaces {
                width: 235px;
            }
            .table__row-price {
                text-align: center;
                width: 90px;
            }
            .table__row-button {
                text-align: center;
               .pink-btn--form {
                    font-size: 13px;
                    font-weight: 500;
                    text-transform: uppercase;
                    width: 100px;
                    height: 30px;
                    line-height: 30px;
               } 
            }
           .table__cel-end {
                color: map_get($gray-light, default);
           }
           .table__cel {
                td {
                    vertical-align: middle;
                    padding-left: 30px
                }
                .table__row-button {
                    padding: 5px 0 5px;
                }
                .table__row-price {
                    padding-left: 0;
                }
                .table__row-nameClasses {
                    color: map_get($gray-light, default);
                }
           } 
        }
    }
}


.gallery {
    // padding-top: 30px;
    .gallery-grid-wrap {
        height: 560px;
        overflow: hidden;
        transition: all .4s;
    }
    .gallery-grid {
        &__item {
            width: 110px;
            height: 140px;
        }
    }
}

@import 'parts/calendar-booking';



